const targetElement = document.getElementById("s1-p1");

const myObserver = new IntersectionObserver(
    (entries) => {
        const bgImg = document.querySelector('.bg-img');
        const bgVideo = document.querySelector('.bg-video');
        if (entries[0].isIntersecting) {
            
            setTimeout(function () {
                bgImg.classList.add("d-none");
                bgVideo.classList.remove("d-none");
            }, 2000);
        }
        else{
            bgVideo.pause();
            bgVideo.currentTime = 0;
            bgVideo.load();

            bgImg.classList.remove("d-none");
            bgVideo.classList.add("d-none");
        }
    }
);

myObserver.observe(targetElement); //Seleccionamos el elemento a observar